$esrbmodal:(

main:(
    pad: 0 !important
),

modalDialog: ( 
    width: 60%,
    posTop: calc(50% - 283px - 30px),
    tabWidth: 94%,
    margin: 10px auto !important
),

modalContent: ( 
    boxShadow: none,
    borderRadius: 0,
    pad:1.42857em,
    bgndColor:#949698,
    bgndImage:url(#{$brandImgPath}/jurassic-img-txt-bg.jpg),
    bgndSize:cover,
    bgndRepeat:no-repeat,
    bgndPosition:bottom center,
    mobbgndImage:none !important
),

modalHeader:(
    borderRadius: 0px,//em(30),
    borderBottom:0 none !important,
    height:auto,
    bgndClip: padding-box
),
heading: (
    txtAlign: center,
    txtColor: #191919,
    fntSize: em(65),
    margin: 0,
    fntFamily: null,
    fntWeight: null
),

modalheaderClose: (
   opacity: 0.5,
   txtColor: #191919,
   closeContent: "\f00d",
   fntFamily: 'Font Awesome',
   closetxtColor: #191919,
   float: right,
   fntSize: 2.342em,
   fntWeight: 400,
   lineHeight: 1,
   textShadow: none,
   closeOpacity: 1,
   hovertxtColor: #191919,
   marginRight: 0
),

modalbody: ( 
   marginRight: 0,
   mobPadding: 0,
   tabPadding: 0,
   tabWidth: auto,
   tabFloat: none,
   tabMargin: auto,
   tabbgndSize: contain
),

 modalbodyH2: (
   txtAlign: center,
   txtColor: #191919,
   fntSize: em(65),
   margin: 0,
   fntFamily: null,
   fntWeight: null
),

playRetailer: (
    bgndColor: transparent
),

playretailerulList: (
    bgnd: $white,
    borderRadius: em(15),
    display:block
),

gallerytileUl:(
   display: inline-table,
   txtAlign: center,
   margin: em(40) 0,
   pad: 0,
   mobmargin: 0              
),

gallerytileulList: ( 
   display:inline-block,
   float:none,
   margin:0 em(10),
   pad:0,
   overflow:hidden,
   width:31%,
   mobWidth:47%,
   mobMargin:em(16) em(7),
   typeDisplay:inline-block,
   marginLeft:0,
   marginRight: 0
),

modalbodyP: (  
   spanContent:"",
   mobfntSize: em(18), 
   txtAlign:center,
   txtColor:#191919,
   fntSize:em(22),
   margin:0,
   fntWeight:null,
   paddingBottom:em(3),
   fntFamily:null,
   lineHeight:em(20)
),

modalbodyLink: (
    txtDecoration:none,
    imgWidth:100%,
    imgHeight:5.9em
),

modalbtnWrap: (
    width: 100%,
    display: block,
    txtAlign: center
),

modalbodygobackcontinueBtn:( 
    txtAlign:center,
    pad: em(5) em(20),
    display:inline-block,
    borderColor:em(2) solid #f10196,
    borderRadius: em(30),
    fntSize:em(24),
    fntFamily:Arial,
    fntWeight:700,
    transition: 0.5s
),

 modalbodygobackBtn:( 
    margin: em(10),
    display: inline-block,
    txtColor: $violet,
    bgnd: $white,
    hovertxtColor: $violet,
    hoverBgnd: $white,
    hoverborderColor: em(2) solid $violet
 ),
 
 modalbodycontinueBtn: (  
   margin: em(10),
   txtColor: $white,
   bgnd: $violet,
   hoverBgnd: #f10196,
   hovertxtColor: $white,
   hoverborderColor: em(2) solid $violet,
   sameasSecondaryBtn: true,
   secondaryPadding: em(13, 30) em(35, 30) em(15, 30) em(35, 30),
   isArrowWithBtn: false,
 ),

 modalFooter: (  
   borderTop: 0
 )

);
