
$videosLanding:(
	main:(
		isViewAllHide: true,
		isRoundedPlayIcon: true,
		playerBarBgnd: $black,
		playerBarHeight: 0
	),
	homePage:(
		pageBgnd: '#{$brandImgPath}/video_bgnd.jpg',
		isBgnd: false,
		playerPadding: 0,
	),
	videosPage:(

	),
	playIcon: (
        isRoundedCircle: true,
        bgnd:#bc3227,
		color:#2e2a25,
		fontSize: 35px,
		tabFontSize: 30px,
		mobFontSize: 30px
    ),
	arrows:(
		color:#ad3d2f,
		mobColor: #ad3d2f,
		fontSize: 80px,
		background: null,
		isRoundBgnd: false,
		leftPos:20px,
		rightPos: 20px,
		opacity:1
	),
	nowPlaying: (
		borderRadius: 4px,
		padding: 4px 7px,
		margin: 0,
		fontSize: 14px,
		backgroundImage: none,
		backgroundColor: #1268b3,
		color: $black,
		borderColor: #1268b3,
		fontFamily: Arial
	),
	heading : (
        bgColor: transparent,
		color : #e0e0e0,
        fontSize: em(65),
        fontFamily: "DecoturaICG",
		fontWeight: $fontMedium,
		align: left,
		margin:0,
		padding: 0
	),
    description : (
        bgColor: transparent,
        fontSize: em(20),
        color : #ffffff,
        fontFamily: "Sunflower",
		fontWeight: $fontMedium,
		align: left,
		padding: 0,
		margin:em(5,20) 0 0,
	    mobilepadding:0
	),
	playerBarHeading:(
		color : #4e95cc,
        fontSize: em(40),
        fontFamily: "DecoturaICG",
		fontWeight: $fontMedium,
		align: left,
		margin:0,
		padding: 0
	),
	playerBarDescription:(
	  fontSize: null,
        color : null,
        fontFamily: "Sunflower",
		fontWeight: $fontMedium,
		align: left,
		padding: 0,
	    mobilepadding:0
	),
    thumbnailHeading: (
        bgColor: transparent,
		color : #e0e0e0,
        fontSize: em(40),
        fontFamily: "DecoturaICG",
		fontWeight: $fontMedium,
		align: left,
		padding: em(20,40) em(20,40) 0 0,
		tabPadding: em(20,40) em(20,40) 0,
		mobPadding: em(20,40) em(20,40) 0 0,
		margin: em(20,40) 0 0,
	),
	galleryTile: (
		tilePadding: 0,
		tabTilePadding: 0,
		mobTilePadding: 0
	),
	listTitle : (
		bgColor: transparent,
		color : #4e95cc,
        fontSize: em(22),
        fontFamily: "Sunflower",
		fontWeight: $fontMedium,
		align: left,
		margin:5px 0 0,
		padding: 0
	),
	showMore: (
		margin:0,
		padding: null
	)
);