.play-category-details {
    background: mapping($chardetails,main,bgnd);
    padding: mapping($chardetails,main,pad);
}

.cda-banner{
    &.char-det{
        width: mapping($chardetails,cdaBanner,width);
        margin: mapping($chardetails,cdaBanner,margin);
    }
    
    position: mapping($chardetails,cdaBanner,pos);
    background : mapping($chardetails,cdaBanner,bgnd);
    .carousel-slides{
        padding: mapping($chardetails,bannerCarousel,pad);
        li{
            position: mapping($chardetails,bannerCarousel,lstpos);
            img {
                @extend .col-xs-12;
                @extend .col-sm-12;
                @extend .col-md-6;
                @media #{medium-up}{
                    width: mapping($chardetails,bannerCarousel,tabimgWidth); 
                }
            }
            .img-right{
                img{
                    float: mapping($chardetails,bannerCarousel,imgfloat);
                }
            }


        }
        .slick-prev,
        .slick-next{
            top: mapping($chardetails,carouselslickprevNext,posTop);
            @media #{$small-up}{
                top: mapping($chardetails,carouselslickprevNext,tabposTop);
            }
            &:before {
                font-family: mapping($chardetails,carouselslickprevNext,fntFamily);
                background-color: mapping($chardetails,carouselslickprevNext,bgndColor);
                border-radius: mapping($chardetails,carouselslickprevNext,borderRadius);
                padding: mapping($chardetails,carouselslickprevNext,pad);
                }
        }
        .slick-prev {
            left: mapping($chardetails,carouselslickPrev,posLeft);
            &:before {
                content: mapping($chardetails,carouselslickPrev,prevContent);
            }
        }
        .slick-next {
                right: mapping($chardetails,carouselslickNext,posRight);
            &:before {
                content: mapping($chardetails,carouselslickNext,nextContent);
            }
        }
        
    }


    .banner-copy{
        @media #{$medium-up}{
            width: mapping($chardetails,bannerCopy,tabWidth);
        }
        padding: mapping($chardetails,bannerCopy,pad);
        background: mapping($chardetails,bannerCopy,bgnd);
        @extend .col-xs-12;
        @extend .col-sm-12;
        @extend .col-md-6;
        
        h2{color:mapping($chardetails,bannercopyH2,txtColor);}
        p{color: mapping($chardetails,bannercopyP,txtColor);}
    }
}
.banner-copy{
    &[class*='top-'],
     &[class*='bottom-']{
        transform: mapping($chardetails,bannerCopy,posTransform);
        -webkit-transform: mapping($chardetails,bannerCopy,posTransform);
        -ms-transform: mapping($chardetails,bannerCopy,posTransform);
        top: mapping($chardetails,bannerCopy,posTop);
        left: mapping($chardetails,bannerCopy,posLeft);
    }
    &.top-left{
        left: mapping($chardetails,bannercopytopLeft,posLeft);
        top: mapping($chardetails,bannercopytopLeft,posTop);
    }
    &.top-right{
        right: mapping($chardetails,bannercopytopRight,posRight);
        top: mapping($chardetails,bannercopytopRight,posTop);
    }
    &.bottom-left{
        left: mapping($chardetails,bannercopybottomLeft,posLeft);
        bottom: mapping($chardetails,bannercopybottomLeft,posBottom);
    }
    &.bottom-right{
        right: mapping($chardetails,bannercopybottomRight,posRight);
        bottom: mapping($chardetails,bannercopybottomRight,posBottom);
    }
    &.top-center{
        left: mapping($chardetails,bannercopytopCenter,posLeft);
        top: mapping($chardetails,bannercopytopCenter,posTop);
        transform: mapping($chardetails,bannercopytopCenter,posTransform);
        -webkit-transform: mapping($chardetails,bannercopytopCenter,posTransform);
        -ms-transform: mapping($chardetails,bannercopytopCenter,posTransform);
    }
    &.bottom-center{

    }
}
