$header:(
  main:(
    bgnd: url($header-bg-path) 0 0 repeat-x,
    minHeight: em(77),
    posTop: 3.6em,
    posLeft:0,
    zIndex: 1025,
    pos: fixed,
    width: 100%,
    lineHeight:em(14),
    bgndSize: contain,  
    mobBgndSize: cover
  ),
  childDiv: (
    width: auto
  ),
  logo:(
    pos: absolute,
    zIndex: 9999
  ),
  logoImage:(
    margin:em(10) 0 0 em(5),
    width: em(130),
    mobMargin:em(10) 0 0 em(38),
    mobWidth: em(140)
  )
);
