$accordion:(
  main: (
    bgndColor: $darkestGrey,
    pad: 0 em(30) em(70),
    titleBlockPad: em(50) em(30)
  ),
  withbg: (
    bgndImg: $slateGrey url($accordion-bg) right center no-repeat,
    bgndSize: contain,
    fntFamily: 'DecoturaICG',
    fntSize: em(30)
  ),
  title:(
    margin: 0px,
    pad: 1.5em 0,
    fntWeight: normal,
    borderBottom: null,
    borderTop: $grey 1px solid,
    txtColor: $lightGrey,
    fntSize: em(22),
    fntFamily: 'Sunflower'
    ),

  view: (
    fntSize: em(1),
    lineHeight: 200%,
    fntWeight: normal,
    txtAlign: left

  ),  
  accContent:(
    display: none,
    pad: 0 0 em(20)
  ),
  acccontentDefault:(
      display: block
  ),
  accContentP: (
    fntWeight: normal,
    txtColor: $lightGrey,
    fntSize: em(22),
    fntFamily: 'Sunflower',
    pad: 0 0 em(10),
    margin: 0
  ),
  accContentOlLi: (
    fntWeight: normal,
    txtColor: $lightGrey,
    fntSize: em(18),
    fntFamily: 'Sunflower',
    pad: 0 0 em(10),
    margin: 0
  ),
  accArrow:(
      marginLeft:0,
      display:inline-block,
      pos: absolute,
      posRight: em(10),
      iconColor: $red,
      cursor: pointer,
      brdr: $red 2px solid,
      brdrRadius: 100%,
      iconWidth: 40px,
      iconHeight: 40px
  )
);