.founder-font-extend{
  font-family: 'MattelFounders',Arial, Helvetica, sans-serif;
  font-weight: $fontMedium;
  *{
    font-family: inherit;  
  }
}
.brand-list-item {
  padding: em(30) em(10);
  margin: 0;
  @media #{$small-up} {
    padding: em(50) em(10);
  }
  li {
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    vertical-align: top;
    a {
      img {
          max-height: em(178.5);
          margin: auto;
      }
      span{
        display: block;
        font-size: em(22);
        // font-weight: $fontSemiBold;
        color: $black;;
        margin: em(15,22) 0 em(10,22);
        max-width: 90%;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
}
.brand-list-wrapper{
  @extend .founder-font-extend;
  h2{
    font-size: em(48);
    color: $pelorous-blue;
    text-align: center;
    margin-bottom: 0;
  }
}
.welcome-banner{
  .play-page-title{
    @extend .founder-font-extend;
    text-align: center;
    padding: em(40) em(20);
    &>div{
      padding: 0;
      width: auto;
      float: none;
    }
    h1{
      color: $yellow;
      line-height: 1;
      font-size: em(60);
      margin: 0;
      @media #{$medium-up}{
        font-size: em(80);
      }
    }
    p{
      color:$white;
      font-size: em(22);
      margin-bottom: 0;
      margin-top: 15px;
      font-weight: $fontMedium;
    }
  }
}
