$cdabanner:(

main:(
    pos: relative,
    bgnd: #484c55,
    isMobileDesignForiPadPortrait: true
),

carouselSlides: ( 
    paddingBottom: em(15),
    borderBottom: em(15) solid rgba(202, 166, 230, 1),
    tabpaddingBottom: 0,
    tabborderBottom: none
),

carouselslidesList: ( 
    pos: relative,
    margin: 0 em(7)
),

bannerCopy:(
    pad: em(25),
    width: 100%,
    wordBreak: break-word,
    txtAlign: left,
    tabWidth: 45%,
    tabpadding: em(35) em(50),
    tabpos: relative,
    tabposTop: 0,
    tabposLeft: 0,
    tabposRight: 0,
    tabposBottom: 0,
    tabtxtAlign: left,
    tabBgnd: transparent,
    moduledestxtColor: $black,
    tabModuledestxtColor: $white,
    mobModuledestxtColor: $white,
    mobPadding: em(20) em(20)
),

bannercopyH2: (
  margin: 0
),

cdabannerImage: (
  display: block,
  margin: auto
),

playcontainerslickPrev: (    
    marginLeft: 30px
),

playcontainerslickNext: (
    marginRight: 30px
),

playcontainerslickprevNext: (
    bgnd: null, //$slick-arrow-bg,
    borderRadius: 0, //50%,
    fntFamily: 'Font Awesome' !important,
    pad: 0,
    txtColor: null, //$slick-arrow-color,
    fntSize: 70px,
    beforeBgnd: none,
    tabposTop: 23%,
    tabDisplay: none !important
),

slicknextBefore: (   
   nextContent: "\f105",
   marginLeft: 5px,
   opacity: 1
 ),

 slickprevBefore: (
    prevContent: "\f104",
    marginRight: 5px,
    opacity: 1
),

slickdotsActive: (
    bgnd: null //$slick-dots-active-btn-bg
),

slickdotsButton: (
    bgndColor: null, // $litviolet,
    width: em(20),
    height: em(20),
    opacity: 1,
    tabbgndColor: null // $litviolet 
),


bannercopySpan:(
    bgndColor: null,
    borderColor: null, //em(3) solid $banner-border,
    txtColor: null, //$banner-txt,
    fntSize: null, //em(19),
    hoverBgnd: null, //$banner-border !important,
    hoverborderColor: null, //em(3) solid $banner-bg,
    display: inline-block
),

bannercopymoduleDesc: (
    txtColor: $black,
    fntFamily: 'Sunflower',
    fntWeight: $fontMedium,
    fntSize: em(20),
    letterSpacing: 0,
    margin: em(30,20) 0 em(20,20),
    tabtxtColor: $black
),

bannercopymoduleSpan: (
    display: block,
    txtColor: $module-txt,
    fntFamily: 'Arial',
    fntWeight: 'Bold',
    fntSsize: inherit,
    tabtxtColor: $black 
),

bannercopymoduleTitle:( 
    txtColor: $black,
    tabTxtColor: $white,
    mobTxtColor: $white,
    maxWidth: 35vw,
    fntSize: em(65),
    mobfntSize: em(38)
),

bannercopyPos:( 
  posTransform: none,
  posTop: auto,
  posLeft: auto
),

bannercopypostopLeft:( 
     posLeft: 7%,
     posTop: 8%
),

bannercopypostopRight:(    
     posRight: 7%,
     posTop: 8%
),


bannercopyposbottomLeft:(   
    posLeft: 7%,
    posBottom: 8% 
),

bannercopyposbottomRight:( 
    posRight: 7%,
    posBottom: 8%
),

bannercopyposmiddleLeft:( 
    posLeft: 7%,
    posTop: 33%
),

bannercopyposmiddleRight:( 
      posRight: 7%,
      posTop: 33%
),

bannercopypostopCenter:( 
     mobposLeft: 5.686274509803922%,
     mobposTop: 50%,
     mobposTransform : translateY(-50%)
),
bannercopyposbottomCenter:(
    mobposLeft: 50%,
    mobposBottom: 8%,
    mobposTransform: translateX(-50%)
),
bannercopyposVcenter:(
    mobposLeft: 50%,
    mobposTop: 50%,
    mobposTransform: translateX(-50%) translateY(-50%),
),
bannercopyBg:(
    mobBgnd: transparent
),
bannercopymodTitle:(
   mobMargin: 0,
   fntSize: 1.8em,
   mobfntSize: 1.5em,
   tabfntSize: 2em
),
bannercopymodSubtitle:(
   mobfntSize: 1.3em,
   tabfntSize: em(22),
   mobtxtAlign: center
),
heroimageBanner:(
   txtAlign: center
),  
heroimageCrm:(
  display: inline-block,
  pos: relative,
  txtAlign: left
)
);
