$chardetailsslider:(

main:(
    bgnd: url('/public/img/product-bg_0.jpg'),
    pad: 5% 0,
    pos: relative
),

container: ( 
    bgndColor: $white,
    pad: 0px,
    mobWidth: 95%,
    tabWidth: 80%
),

 chardetailContent: (
    marginLeft:0,
    marginRight:0
),

chardetailImage: (
    txtAlign: center,
    pad:0,
    float:right,
    tabmaxWidth: 80%,
    tabpaddingTop:em(15)
 ),

 chardetailDesc: (  
    paddingBottom: 0,
    pad: em(70)
),

chardetaildescH1: (
    txtColor:$pink,
    fntFamily: "Arial",
    fntWeight: $fontBold,
    smalltxtColor: $violet
),

chardetaildescP: (
    txtColor: $violet,
    fntFamily: "Arial",
    fntWeight: $fontRegular
),

 cdPrev: (   
       posLeft: 60px,
       desposLeft: 0,
       tabposLeft:45px,
       mobposLeft:-12px,
       prevContent: "\f104"    
  ),

  cdNext: (   
       posRight: 60px,
       desposRight: 0,
       tabposRight: 45px,
       mobposRight: -12px,
       nextContent: "\f105"    
  ),


  cdprevNext: (
    posTop: 50%,
    width: 70px,
    height: 70px,
    borderRadius: 50%,
    pad: 0,
    bgnd: #C1A7E2,
    hoverBgnd: #C1A7E2,
    tabposTop: 30%
  ),

  cdprevnextBefore: (
    fntFamily: 'Font Awesome',
    bgndColor: $litviolet,
    borderRadius: 50%,
    pad:0,
    opacity: 0.9,
    fntSize: 70px,
    bgnd: transparent
  )

);