.play-text {
    font-size: 1em;
    font-family: swiss;
    font-variant: normal;
    font-weight: normal;
    font-style: normal;
    text-align: right;
    white-space: normal;
    text-transform: none;
    text-decoration: none;
    line-height: normal;
    letter-spacing: normal
}




.swissBlackRounded {
    font-family: Swiss !important;
    font-weight: $fontRegular !important;
}

.swissBoldRounded {
    font-family: Swiss !important;
    font-weight: $fontBold !important;
}

.arialRounded {
    font-family: "Arial-Rounded", Arial, sans-serif !important;
    font-weight: $fontBold !important;
}


.loading-icon-extend {
    content: "\f110" !important;
    font-family: "Font Awesome";
    font-weight: 900;
    -webkit-font-smoothing: antialiased;
    display: inline-block !important;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    font-size: 56px;
    color: $violet;
    text-align: center;
    -moz-animation: fa-spin 2s infinite linear;
    -webkit-animation: fa-spin 2s infinite linear;
    animation: fa-spin 2s infinite linear;
}

.data-loading {
    position: relative;
    min-height: 31.71429em;
    float: left;
    width: 100%;
}

.data-loading:after {
    @extend .loading-icon-extend;
    margin-left: 5px;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    left: 0;
    right: 0;
}

.next-data-loading {
    position: relative;
    overflow: hidden
}

.next-data-loading:after {
    @extend .loading-icon-extend;
    margin: 20px auto;
    width: 100%;
    margin: 20px 0;
    background: none;
    height: auto;
    position: static;
}

//buttons
//btn-colors
$icegreen:#C4D600;
$dark-pink:#f10196;

.btn-theme-play {
    color: $white;
    background-color: $pink;
    border-color: $icegreen;
    &:hover,
    &:focus

    /*, &.active*/
        {
        background-color: $icegreen;
        border-color: $pink;
    }
}

.btn-show-more {
    cursor: pointer;
    min-width: 240px;
    padding: 28px;
    border-width: 5px;
    line-height: 1.4em;
    display: inline-block;

    font-size: em(19);
    text-align: center;
    text-transform: uppercase;
    @include border-radius(em(50));
    border-style: solid;
    font-weight: $fontLight;

    &:before {
        font-family: 'Font Awesome';
        /*font-weight:700;*/
        content: "\f055";
        margin: 5px;
        vertical-align: middle;
    }

    &.active:before {
        content: "\f056";
    }

    @extend .btn-theme-play;

}

:link.btn-view-all {
    color: $pink;
}

.btn-view-all {
    @if(mapping($layout,viewAll,isOverwritePrimaryBtn), 'true'){
        @extend .theme-btn;
    } @else{
        cursor: pointer;
        max-width: 100%;
        min-width: 162px;
        margin: 0;
        padding: 0.78em 1.45em;
        border-width: em(3);
        display: inline-block;
    
        font-size: em(19);
        font-weight: $fontLight;
        white-space: normal;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        border-style: solid;
    
        border-color: $icegreen;
        color: $pink;
        background: $white;
    
        @include border-radius(2em !important);
        &:hover,
        &:focus
    
        /*, &.active */
            {
            border-color: $pink;
            color: $white;
            background: $icegreen;
        }
    }
}

.btn-primary {
    cursor: pointer;
    max-width: 100%;
    min-width: 150px;
    padding: 5px 15px;
    border-width: 4px;
    display: inline-block !important;

    font-size: em(21);
    font-family: "Arial-Rounded", Arial, sans-serif;
    font-weight: $fontBold;
    text-align: center;
    text-decoration: none;
    border-style: solid;

    border-color: $pink !important;
    background-color: $white !important;
    color: $violet !important;
    @include border-radius(2em !important);

    &:hover,
    &:focus,
    &.active {
        border-color: $violet !important;
        color: $pink !important;
    }
}

//esrb-model-btns
.btn-cancel,
.btn-continue {
    cursor: pointer;
    margin: em(10);
    padding: em(5) em(20);
    display: inline-block;
    border-width: em(2);

    font-size: em(24);
    font-family: "Arial-Rounded", Arial, sans-serif;
    font-weight: $fontBold;
    text-align: center;
    border-style: solid;
    @include border-radius(2em !important);
}

.btn-continue {
    color: $white;
    background: $violet;
    border-color: $dark-pink;

    &:hover,
    &:focus,
    &.active {
        background: $dark-pink;
        color: $white;
        border-color: $violet;
    }
}

.btn-cancel {
    color: $violet;
    background: $white;
    border-color: $dark-pink;

    &:hover,
    &:focus,
    &.active {
        color: $pink;
        background: $white;
        border-color: $violet
    }
}

.btn-cta {
    padding: 20px 90px;
    border-width: 4.5px;
    border-style: solid;
    font-family: "Arial-Rounded", Arial, sans-serif;
    font-size: em(19);
    font-weight: $fontBold;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 1;
    @include border-radius(2em !important);

    &:link {
        color: $white;
    }

    @extend .btn-theme-play;
}

:link.btn-play-default {
    color: $white;
}

.btn-play-default {
    cursor: pointer;
    display: inline-block;

    max-width: 100%;
    min-width: 150px;
    padding: em(10) em(20);

    border-width: em(4.5);
    border-style: solid;
    @include border-radius(2em);

    font-size: 1em;
    font-family: "Swiss";
    font-weight: "$fontRegular";

    text-align: center;
    text-decoration: none;
    text-transform: uppercase;

    white-space: normal;
    letter-spacing: 1px;
    line-height: 1;
    @extend .btn-theme-play;
}

//margins
.margin-LR0 {
    margin-left: 0 !important;
    margin-right: 0 !important;
}

.global-mattel-header {
    .slick-arrow {
        font-size: 1em;
        top: 45%;

        &.slick-next:before,
        &.slick-prev:before {
            content: "\f359";
            @include font-awesome();
            color: $yellow;
            vertical-align: top;
            font-size: em(58);
            background: none;
            opacity: 1;

            @media #{$medium-down} {
                font-size: em(48);
            }
        }

        &.slick-next:before {
            content: "\f35a";
        }

    }

    .slick-prev:before,
    .slick-next:before {
        opacity: 1;
    }

    .slick-dots li {
        width: auto;
        height: auto;
        margin: 0;

        button {
            width: 18.5px;
            height: 18px;
            margin: 5px 9px;
            background-color: $athens-grey;
        }

        &.slick-active {
            button {
                background-color: $yellow;
            }
        }
    }
}

.slick-arrow-extend {
    .slick-arrow {
        font-size: 1em;
        width: 35px;
        height: 35px;

        &.slick-next:before,
        &.slick-prev:before {
            content: "\f104";
            @include font-awesome();
            color: $white;
            vertical-align: top;
            font-size: em(38);
            line-height: inherit;
            background: none;
        }

        &.slick-prev {
            left: -25px;
        }

        &.slick-next {
            right: -30px;

            &:before {
                content: "\f105";
            }
        }
    }
}

