$fontPath : "../css/fonts";



@font-face {
    font-family: "DecoturaICG";
    //src: url(#{$fontPath}/DecoturaICG.eot);
    //src: url(#{$fontPath}/Swiss721Blk.eot?#iefix) format("embedded-opentype"),
	//url(#{$fontPath}/Swiss721Blk.woff2) format("woff2"),url(#{$fontPath}/Swiss721Blk.woff) format("woff"),url(#{$fontPath}/Swiss721Blk.ttf) format("truetype"),url(#{$fontPath}/Swiss721Blk.svg) format("svg");
    src: url(#{$fontPath}/DecoturaICG.ttf) format("truetype");
