@font-face {
    font-family: "Sunflower";
    src: url(#{$fontPath}/sunflower-v3-latin-300.eot);
    src: url(#{$fontPath}/sunflower-v3-latin-300.eot?#iefix) format("embedded-opentype"),
	url(#{$fontPath}/sunflower-v3-latin-300.woff2) format("woff2"),url(#{$fontPath}/sunflower-v3-latin-300.woff) format("woff"),url(#{$fontPath}/sunflower-v3-latin-300.ttf) format("truetype"),url(#{$fontPath}/sunflower-v3-latin-300.svg) format("svg"),
	url(#{$fontPath}/sunflower-v3-latin-300.ttf) format("truetype"),url(#{$fontPath}/sunflower-v3-latin-300.svg) format("svg");
	font-weight: $fontLight;
}

@font-face {
