.country-drop-down-menu {
  margin: mapping($countrydropdown,dropdownMenu,margin);
  padding: mapping($countrydropdown,dropdownMenu,pad);
  width:  mapping($countrydropdown,dropdownMenu,width);
  max-width: mapping($countrydropdown,dropdownMenu,maxWidth);
  text-align: mapping($countrydropdown,dropdownMenu,txtAlign);
  font-size: mapping($countrydropdown,dropdownMenu,fntSize);
  @media #{$small-down} {
  	margin-bottom: mapping($countrydropdown,dropdownMenu,mobmarginBottom);
  }
  @media #{$medium-down} {
  	margin-bottom: mapping($countrydropdown,dropdownMenu,tabmarginBottom);
  }
  li {
    position: mapping($countrydropdown,dropdownList,pos);
    padding:  mapping($countrydropdown,dropdownList,pad);
    background: mapping($countrydropdown,dropdownList,bgnd);
    list-style: mapping($countrydropdown,dropdownList,lstStyle);
    display: mapping($countrydropdown,dropdownList,display);
    width: mapping($countrydropdown,dropdownList,width);
  }
  span {
    border: mapping($countrydropdown,dropdownSpan,brdr);
    color: mapping($countrydropdown,dropdownSpan,txtColor);
    display: mapping($countrydropdown,dropdownSpan,display);
    font-size: mapping($countrydropdown,dropdownSpan,fntSize);
    padding: mapping($countrydropdown,dropdownSpan,pad);
    background: mapping($countrydropdown,dropdownSpan,bgnd);
    @include transition(mapping($countrydropdown,dropdownSpan,spantransition));
    &:hover, &.open {
      background: mapping($countrydropdown,dropdownSpan,hoverbgnd);
      color: mapping($countrydropdown,dropdownSpan,hoverColor);
    }
    &:after {
      content: mapping($countrydropdown,dropdownSpan,spanContent);
      /*'\e259';*/
      font-family: mapping($countrydropdown,dropdownSpan,fntFamily);
      position: mapping($countrydropdown,dropdownSpan,pos);
      top: mapping($countrydropdown,dropdownSpan,posTop);
      right: mapping($countrydropdown,dropdownSpan,posRight);
      width: mapping($countrydropdown,dropdownSpan,width);
      height: mapping($countrydropdown,dropdownSpan,height);
      padding-top: mapping($countrydropdown,dropdownSpan,paddingTop);
      text-align: mapping($countrydropdown,dropdownSpan,txtAlign);
      background: mapping($countrydropdown,dropdownSpan,spanafterBgnd);
      color: mapping($countrydropdown,dropdownSpan,spanaftertxtColor);
      box-sizing: mapping($countrydropdown,dropdownSpan,boxSizing);
    }
  }
  ul {
    position: mapping($countrydropdown,dropdownUl,pos);
    width: mapping($countrydropdown,dropdownUl,width);
    padding: mapping($countrydropdown,dropdownUl,pad);
    z-index: mapping($countrydropdown,dropdownUl,z-index);
    height: mapping($countrydropdown,dropdownUl,height);
  }
  a {
    display: mapping($countrydropdown,dropdownLink,display);
    padding: mapping($countrydropdown,dropdownLink,pad);
    text-decoration: mapping($countrydropdown,dropdownLink,textDecoration);
    color: mapping($countrydropdown,dropdownLink,txtColor);
    font-size: mapping($countrydropdown,dropdownLink,fntSize);
    @include transition(mapping($countrydropdown,dropdownLink,transition));
    &:hover {
      background: mapping($countrydropdown,dropdownLink,hoverBgnd);
      color: mapping($countrydropdown,dropdownLink,hovertxtColor);
    }
  }
  * {
    cursor: mapping($countrydropdown,dropdownAll,point);
  }
  [menu-level="1"] {
    display: mapping($countrydropdown,menuLevel,display);
  }
  .open[menu-level="1"] {
    display: mapping($countrydropdown,menuLevel,menuopenDisplay);
    height: mapping($countrydropdown,menuLevel,height);
    overflow: mapping($countrydropdown,menuLevel,overflow);
    //overflow-y: scroll;
    &.overflow-y{
    	overflow-y: mapping($countrydropdown,menuLevel,overflowY);
    }
    &::-webkit-scrollbar-thumb {
        @include box-shadow(mapping($countrydropdown,menuLevel,boxShadow));
      -webkit-box-shadow: mapping($countrydropdown,menuLevel,webkitboxShadow);
      border-radius: mapping($countrydropdown,menuLevel,borderRadius);
      background-color: mapping($countrydropdown,menuLevel,bgndColor);
    }
    &::-webkit-scrollbar {
      width: mapping($countrydropdown,menuLevel,webkitscrollWidth);
      background-color: mapping($countrydropdown,menuLevel,webkitscrollbgndColor);
      height: mapping($countrydropdown,menuLevel,webkitscrollHeight);
    }
  }
}
