$characterlanding:(

childDiv:(
    pad: 0,
    txtAlign: center,
    pos: relative
   ),
  charSection: (
      txtAlign: center,
      pos:relative
  ),

  charSpan: (   
    bgndColor: $character-label-bg,
    borderRadius: 4px,
    txtAlign: center,
    pad: 0 em(4),
    fntSize: em(26),
    txtcolor: $character-label,
    pos: absolute,
    posBottom: em(20),
    posLeft: 50%,
    posTransform: translateX(-50%)
  )

);