//title
.gallery-headings {
    padding: if(mapping($layout,main,titleMargin), mapping($layout,main,titleMargin), em(55) em(65) em(50) em(65));
    @media #{$medium-down} {
        padding: mapping($layout,main,tabTitleMargin);
    }
    @media #{$small-down} {
        padding: mapping($layout,main,mobTitleMargin);
    }
    h2 {
        font-family: mapping($gallery,heading,fontFamily);
        font-size: if(mapping($gallery,heading,fontSize),mapping($gallery,heading,fontSize),em(38));
        color: if(mapping($gallery,heading,color),mapping($gallery,heading,color),$page-title);
        font-weight: mapping($gallery,heading,fontWeight);
        text-transform: uppercase;
        margin: 0 0 21px 0;
        text-align: left;
        display: block;
        line-height: 1;
    }

    p {

        font-family: if(mapping($gallery,description,fontFamily),mapping($gallery,description,fontFamily),"Arial");
        font-size: if(mapping($gallery,description,fontSize),mapping($gallery,description,fontSize),1.35714em);
        color: if(mapping($gallery,description,color),mapping($gallery,description,color),$page-sub-title);
        font-weight: if(mapping($gallery,description,fontWeight),mapping($gallery,description,fontWeight),300);
        text-align: left;
        margin: 10px 0 0;
    }
}

.gallery-extnd-tile{
    padding: mapping($layout,main,sectionMargin);
        @media #{$medium-down} {
            padding: mapping($layout,main,tabSectionMargin);
        }
        @media #{$small-down} {
            padding: mapping($layout,main,mobSectionMargin);
        }
        ul {
            &>li {
                // clear: none !important;
                @extend .col-sm-#{mapping($gallery, defaultListCol, mobItem)+1};
                @extend .col-md-#{mapping($gallery, defaultListCol, tabItem)+1};
                @extend .col-lg-#{mapping($gallery, defaultListCol, desItem)+1};
                @media #{$medium-only}{
                    $tabItem : if(mapping($gallery, defaultListCol, tabItemToShow), mapping($gallery, defaultListCol, tabItemToShow), mapping($gallery, defaultListCol, itemToShow));
                    &:nth-of-type(1n+#{$tabItem+1}) {
                        display: none;
                    }
                }
                @media #{$medium-up}{
                    &:nth-of-type(1n+#{mapping($gallery, defaultListCol, itemToShow)+1}) {
                        display: none;
                    }
                }
                @media #{$small-down}{
                    $mobItem : if(mapping($gallery, defaultListCol, mobItemToShow), mapping($gallery, defaultListCol, mobItemToShow), mapping($gallery, defaultListCol, itemToShow));
                    &:nth-of-type(1n+#{$mobItem+1}) {
                        display: none;
                    }
                }
            }
        }
        .view-all{
            @if(mapping($gallery, defaultListCol,isViewAllHide), 'true'){
                display: none !important;
            }
        }
}
//list structure
.gallery-tile-list {
    ul {
        @extend .row;

        .row:before,
        .row:after {
            display: none !important;
        }

        display:block;
        width:100%;
        margin:0;

        &>li {
            @extend .col-sm-3;
            @extend .col-md-3;
            @extend .col-lg-3;
            text-align: center;
            //padding:10px;
            padding: 0;
            margin: 0;
            position: relative;

            &:nth-of-type(1n+5) {
                display: none;
            }

            @media #{$medium-down} {
                width: 50%;
            }

            @media #{$small-down} {
                width: 100%;
                display: inline-block;
            }

            @media #{$medium-up} {
                &.column-3 {
                    width: 33.33333%;
                }

                &.column-4 {
                    width: 25%;
                }
            }

            //character Name
            a {
                display: block;
                position: relative;
                margin: em(15);

                &.btn-primary {
                    color: $white;
                }

                &:hover,
                &:focus,
                &.active {
                    &.gallery-image {
                        img {
                            opacity: 1;
                        }
                    }
                }

                .gallery-image {
                    background-color: $white;

                    img {
                        max-height: 100%;
                        max-width: 100%;

                        .image-container {
                            &:hover {
                                opacity: 1;
                            }
                        }
                    }
                }

                span {
                    @if(mapping($gallery,listTitle,isBgnd), 'false'){
                        background: none;
                        @include border-radius(0);
                    } @else{
                        background-color: $character-label-bg;
                    }
                    @include transition(350ms ease);
                    text-transform: uppercase;
                    //font-size: em(28.01);
                    padding: 0 em(2.5);
                    //color: $character-label;
                    display: block;
                    letter-spacing: em(1);
                    font-family: mapping($gallery,listTitle,fontFamily);
                    text-align:if(mapping($gallery,listTitle,align), mapping($gallery,listTitle,align),left);
                    font-size:if(mapping($gallery,listTitle,fontSize), mapping($gallery,listTitle,fontSize),em(28.01));
                    font-weight: mapping($gallery,listTitle,fontWeight);
                    color: if(mapping($gallery,listTitle, color), mapping($gallery,listTitle,color),$character-label);

                    &.btn-primary {
                        @extend .btn-primary;
                        font-size: em(14);
                        text-align: center;
                        padding: 10px 5px;
                        display: inline-block;
                    }
                }
            }
        }
    }
}

.gallery-image {

    &:hover,
    &:focus,
    &.active {
        img {
            opacity: 0.5;

            &.image-container {
                opacity: 1;
            }
        }
    }
}

.view-all {
    display: none;
    text-align: right;
    margin: em(20) 0;

    @media #{$small-down} {
        text-align: center;
    }
}

.show-more {
    display: none;
    width: 100%;
    text-align: center;
    padding: em(24);

    a {
        @if(mapping($gallery, primaryBtn, isExtendSecondaryBtn), 'true'){
            @extend .secondary-btn;
        } @else{
            @extend .btn-show-more;
        }
    }
}

.margin-LR0 {
    margin-left: 0 !important;
    margin-right: 0 !important;
}

