/**/

.tiles-center {
    padding: em(6);
}

//common structure
.play-tiles-gallery {
    padding: 0;
    width: 100%;
    margin: 0 auto !important;
    background-color: $white;
    @extend .margin-LR0;

    .gallery-title {
        @extend .gallery-headings;
    }

    .gallery-tile {
        @extend .gallery-tile-list;
    }
    &.default{
         
    }

    @media #{$medium-down} {
        width: 100%;
    }
}

//dont include any class into this
.play-tiles-gallery {
    @if(mapping($gallery, main,bgnd)){
        background:url(mapping($gallery, main,bgnd));
    } @else{
        background-color: transparent;
    }

    &.play-character-landing-block {

        ul {
            li {
                display: block !important;
            }

            .playing-overlay {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;

                span {
                    position: absolute;
                    top: 7px;
                    left: 7px;
                    font-size: 1em;
                    font-family: "Arial", sans-serif;
                    white-space: nowrap;
                    display: inline-block;
                    padding: em(7);
                    color: $character-label;
                    background-color: $character-label-bg;
                    z-index: 1;
                    letter-spacing: 1px;
                }
            }
        }
    }

    &#play-game {
        background: url($game-gallery-bg) 0 0 repeat;

        .gallery-tile {
            h2 {
                color: $violet;
                //padding:40px 55px 25px 55px;
                margin: 0;
                padding: 20px 54px 0;
                font-size: 2.5em;
            }
        }

        ul {
            padding: 0 40px 35px 40px;

            li {
                padding: em(15);
            }
        }
    }
}

//gallery component classes
//default
.default {
    .gallery-tile {
        ul {
            &>li {
                
            }
        }
    }
}

//retailers
.play-retailers {
    margin: 0px auto;

    .gallery-title {
        h2 {
            text-align: center;
        }
    }

    .gallery-tile {
        ul {
            @media #{$small-up} {
                padding: 0 em(50);
            }

            &>li {
                &:nth-of-type(1n+5) {
                    display: block;
                }

                margin:0px auto;

                @media #{$small-up} {
                    padding: 0 em(25);
                }

                @media #{$small-down} {
                    width: 50%;
                    float: left;
                    margin: 0;
                }

                a {
                    margin: 0;

                    span {
                        display: inline;

                        &.sr-only {
                            padding: 0;
                            margin: 0;
                        }
                    }

                    .gallery-image {
                        background-color: transparent;
                    }
                }
            }
        }

        .view-all {
            display: none !important;
        }
    }
}


/**/

//character-gallery
.play-characters {
    .gallery-tile {
        ul {
            &>li {
                padding: 0px;
                background-color: $character-bg;

                a {
                    margin: 0;

                    span {
                        z-index: 1024;
                        width: auto;
                        position: absolute;
                        margin: 0 auto;
                        left: 50%;
                        bottom: em(20);
                        @include transform(translateX(-50%));
                        line-height: normal;
                        @include border-radius(8px);
                        display: block;
                        text-align: center;
                        text-transform: uppercase;
                        line-height: 26px;
                        padding: em(8);
                        font-size: em(26);
                        letter-spacing: normal;
                    }

                    &:hover,
                    &:focus,
                    &.active {
                        img {
                            opacity: .7;
                        }
                    }
                }
            }
        }
    }

    .show-more {
        background-color: $show-more-block-bg;
    }
}


/**/

//products-gallery
.play-products {
    background: url($product-gallery-bg-path) 0 0 repeat;
    .gallery-title {
        background-color: if(mapping($gallery,retailerSection,titleBgnd), mapping($gallery,retailerSection,titleBgnd), $white);
        h2 {
            font-weight: $fontLight;
        }

    }
    .gallery-tile {
        padding: if(mapping($layout,main,sectionMargin), mapping($layout,main,sectionMargin), em(50) em(30) em(16));
        @media #{$medium-down} {
            padding: mapping($layout,main,tabSectionMargin);
        }
        @media #{$small-down} {
            padding: mapping($layout,main,mobSectionMargin);
        }
        ul {
            &>li {
                margin-bottom: em(10);

                a {
                    .gallery-image {
                        @if(mapping($gallery,retailerSection,isRounded),'false'){
                            @include border-radius(0);
                        } @else{
                            @include border-radius(50%);
                        }
                        overflow: hidden;
                        background-color: $white;
                        outline: none;

                        &:hover {
                            img {
                                opacity: 1;
                            }
                        }
                    }

                    span {
                        margin-top: em(15);
                        @include border-radius(8px);
                        display: inline-block;
                        text-align: center;
                        font-weight: $fontLight;
                        line-height: 26px;
                        padding: 8px;
                        font-size: 26px;
                        letter-spacing: normal;
                        @if(mapping($gallery, main, isDefaulTitleForRetailer), 'true'){
                            color: mapping($gallery,defaultListTitle,color);
                            background-color: mapping($gallery,defaultListTitle,bgnd);
                            font-family: mapping($gallery,listTitle,fontFamily);
                            text-align:mapping($gallery,listTitle,align);
                            font-size:mapping($gallery,listTitle,fontSize);
                            font-weight: mapping($gallery,listTitle,fontWeight);
                            margin: mapping($gallery,listTitle,margin);
                            padding: mapping($gallery,listTitle,padding);
                            float: if(mapping($gallery,listTitle,align),'left',left);
                        }

                    }
                }
            }
        }
        @extend .gallery-extnd-tile;
    }
}


