//image-gallery
.play-image-gallery {
    .gallery-title {}

    .gallery-tile {
        background-color: $litviolet;
        padding: em(10) em(20);

        ul {
            &>li {
                text-align: left;

                a {
                    &:hover {}

                    span {
                        width: 100%;
                        padding: 0;
                    }
                }
            }
        }
