}

//image-gallery with download button
.play-download {
    .gallery-tile {
        .character-label {
            @extend .gallery-headings;
            padding: 0 em(3) em(3);

            h2 {
                text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
                height: 32px;
                white-space: nowrap;
                font-size: em(21);
                margin: em(0);
                padding: em(3) em(0);
            }

            //height variables
            $font-size:1em;
            $line-height:1.4;
            $lines-to-show:3;

            p {
                display: none;

                &:first-child {
                    display: block;
                    /* Fallback for non-webkit */
                    display: -webkit-box;
                    width: 100%;
                    height: $font-size*$line-height*$lines-to-show;
                    /* Fallback for non-webkit */
                    margin: 0 auto;
                    font-size: $font-size;
                    line-height: $line-height;
                    -webkit-line-clamp: $lines-to-show;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }

            ul {
                &>li {
                    margin-bottom: em(10);

                    a {
                        margin: 5px;
                        color: $white;

                        @media #{$medium-down} {
                            margin: 0;
                        }

                        .gallery-image {
                            background-color: transparent;
                        }
                    }

                    .hover-overlay {
                        position: absolute;
                        top: 0em;
                        left: 0;
                        bottom: 0;
                        right: 0;
                        background: rgba(95, 37, 159, .5);
                        z-index: 99;
                        opacity: 0; //@include css3(transform, scale(1.1));
                        // border-radius:50%;
                        width: 100%;
                        margin: auto;
                        @include transition(350ms ease);

                        button {
                            //@include center();
                            -webkit-transform: translate(-50%, -50%);
                            -moz-transform: translate(-50%, -50%);
                            -ms-transform: translate(-50%, -50%);
                            -o-transform: translate(-50%, -50%);
                            transform: translate(-50%, -50%);
                            top: 50%;
                            left: 50%;
                            position: absolute;
                            padding: .5em;
                            font-family: ArialBold, Arial, sans-serif;
                            font-size: em(12);
                            letter-spacing: 1px;
                        }
                    }

                    &:hover {
                        .hover-overlay {
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}

