/*
** videos.scss
*/

// ooyala player ***
.video-player-wrapper {
    height: 100%;
    z-index: 1;
    margin: 0;
    width: 100%;
    position: relative;
    padding-bottom: 56.25%;
}

.video-player {
    width: 100%;
    height: 100%;
    position: absolute;
    .oo-player {
        padding-top: 56.25% !important;
    }
    &.oo-player-container {
        .oo-action-icon-pause {
            &.oo-animate-pause {
                opacity: 1;
            }
        }
    }
    .oo-control-bar {
        .oo-control-bar-items-wrapper {
            button {
                &.oo-control-bar-item {
                    background: none;
                    /* hide CC option from player */
                    &.oo-closed-caption {
                        display: none;
                    }
                    /* hide share button */
                    .oo-icon-topmenu-share {
                        display: none
                    }
                }
            }
            /* hide ooyala logo from player */
            .oo-watermark {
                display: none;
            }
        }
    }
}

// ***ooyala player
.video-page-title {
    padding: if(mapping($layout,main,titleMargin), mapping($layout,main,titleMargin), em(70) em(325) em(54) em(55));
    display: block;
    @if(mapping($videosLanding,homePage,isBgnd),'false'){
        background: none;
    } @else{
        background-image: url('../img/sunglasses.jpg');
        background-color: $white;
        background-position: 90% center;
        background-size: 15em;
        background-repeat: no-repeat;
    }
    @media #{$medium-down} {
        padding: if(mapping($layout,main,tabTitleMargin), mapping($layout,main,tabTitleMargin), em(62) em(270) em(60) em(50));
    }
    @media #{$small-down} {
        padding: if(mapping($layout,main,mobTitleMargin), mapping($layout,main,mobTitleMargin), em(30) em(20));
        background-image: none;
    }
    h2 {
        font-family: mapping($videosLanding,heading,fontFamily);
        font-weight: mapping($videosLanding,heading,fontWeight);
        background-color: mapping($videosLanding,heading,bgColor);
        padding: mapping($videosLanding,heading,padding);
        font-size: if(mapping($videosLanding,heading,fontSize), mapping($videosLanding,heading,fontSize), em(38.76));
        color:if(mapping($videosLanding,heading,color), mapping($videosLanding,heading,color), $violet);
        margin:if(mapping($videosLanding,heading,margin), mapping($videosLanding,heading,margin), 0 0 21px 0);
        text-align: if(mapping($videosLanding,heading,align), mapping($videosLanding,heading,align), left);
        text-transform: uppercase;
        line-height: 1;
        letter-spacing: 1px;
    }
    p {
        font-family: if(mapping($videosLanding,description,fontFamily), mapping($videosLanding,description,fontFamily),"Arial");
        font-weight: if(mapping($videosLanding,description,fontWeight), mapping($videosLanding,description,fontWeight),300);
        background-color: mapping($videosLanding,description,bgColor);
        padding: if(mapping($videosLanding,description,padding), mapping($videosLanding,description,padding),0);
        font-size: if(mapping($videosLanding,description,fontSize), mapping($videosLanding,description,fontSize), 1.35714em);
        color:if(mapping($videosLanding,description,color), mapping($videosLanding,description,color), $pink);
        margin:if(mapping($videosLanding,description,margin), mapping($videosLanding,description,margin), 0);
        text-align: if(mapping($videosLanding,description,align), mapping($videosLanding,description,align), left);
    }
}

.video-player-container {
    @extend .col-md-12;
    display: block;
    padding: if(mapping($layout,main,sectionMargin), mapping($layout,main,sectionMargin), em(55) em(54));
    @media #{$medium-down} {
        padding: if(mapping($layout,main,tabSectionMargin), mapping($layout,main,tabSectionMargin), em(50));
        overflow: hidden;
    }
    @media #{$small-down} {
        padding: if(mapping($layout,main,mobSectionMargin), mapping($layout,main,mobSectionMargin), em(20));
    }
    background-image:url('../img/pattern-blue-tile.jpg');
    background-position:center center;
    .video-player-in {
        @extend .col-md-9;
        @extend .col-sm-12;
        padding: 0;
    }
    .video-player-label {
        @extend .col-md-12;
        background-color: if(mapping($videosLanding,main, playerBarBgnd), mapping($videosLanding,main, playerBarBgnd), $violet);
        color: $white;
        padding: 20px;
        display: block;
        min-height: if(mapping($videosLanding,main, playerBarHeight), mapping($videosLanding,main, playerBarHeight), 102px);
        h2 {
            letter-spacing: if(mapping($videosLanding,playerBarHeading, letterSpacing), mapping($videosLanding,playerBarHeading, letterSpacing), 2px);
            text-transform: uppercase;
            color: mapping($videosLanding,playerBarHeading, color);
            font-family: if(mapping($videosLanding,playerBarHeading, fontFamily), mapping($videosLanding,playerBarHeading, fontFamily), Arial);
            text-align: if(mapping($videosLanding,playerBarHeading, align), mapping($videosLanding,playerBarHeading, align), center);
            font-weight: mapping($videosLanding,playerBarHeading, fontWeight);
            font-size: if(mapping($videosLanding,playerBarHeading, fontSize), mapping($videosLanding,playerBarHeading, fontSize), em(24));
            padding: mapping($videosLanding,playerBarHeading, padding);
            margin: if(mapping($videosLanding,playerBarHeading, margin), mapping($videosLanding,playerBarHeading, margin), 0);
        }
        p {
            margin-bottom: 0;
            color: mapping($videosLanding,playerBarDescription, color);
            font-family: if(mapping($videosLanding,playerBarDescription, fontFamily), mapping($videosLanding,playerBarDescription, fontFamily), Arial);
            text-align: if(mapping($videosLanding,playerBarDescription, align), mapping($videosLanding,playerBarDescription, align), center);
            font-weight: mapping($videosLanding,playerBarDescription, fontWeight);
            font-size: if(mapping($videosLanding,playerBarDescription, fontSize), mapping($videosLanding,playerBarDescription, fontSize), em(15));
            padding: mapping($videosLanding,playerBarDescription, padding);
            margin: if(mapping($videosLanding,playerBarDescription, margin), mapping($videosLanding,playerBarDescription, margin), 0);
        }
    }
}

.video-vertical-playlist-container {
    @extend .col-md-3;
    padding: em(20);
    padding-bottom: 0;
    //background-color:#5fbee6;
    background-color: $outline-blue;
    @media #{$medium-down} {
        display: none;
    }
    .video-play-option {
        background: $light-blue;
        color: $white;
        font-family: Arial, Helvetica, sans-serif;
        letter-spacing: 1px;
        padding: em(6) em(7) em(6) em(11);
        min-height: 43px;
        input {
            float: right;
        }
    }
    .title-block {
        padding: em(14) 0;
        h2 {
            font-size: em(24);
            letter-spacing: 1.5px;
            margin: 0;
        }
    }
    .video-play-list-area {
        //  height:523.969px;
        @extend .col-md-12;
        overflow-y: scroll;
        padding: 0;
        &::-webkit-scrollbar-thumb {
            @include box-shadow(inset 0 0 6px rgba(0,
            0,
            0,
            0.3));
            @include border-radius(em(10));
            background-color: $violet;
        }
        &::-webkit-scrollbar {
            width: em(10);
            background-color: rgba(0, 0, 0, 0.1);
            height: em(5);
        }
        ul {
            margin: 0;
            &>li {
                margin-bottom: em(20);
                &:last-child {
                    margin-bottom: 0;
                }
                a {
                    display: block;
                    &:link {
                        color: $white;
                    }
                    span {
                        padding: em(10);
                        width: 100%;
                        display: block;
                        background-color: $violet;
                        text-align: center;
                        text-transform: uppercase;
                        font-family: Arial, Helvetica, sans-serif;
                        font-size: 1em;
                        letter-spacing: 1px;
			    color:$white;
                    }
                }
            }
        }
    }
}

.video-category-filter {
    @extend .col-md-12;
    padding: em(30) em(54);
    @media #{$medium-down} {
        padding: em(30) em(20);
        .enable-carousel-nav {
            padding: 0;
        }
    }
    .cda-banner {
        background: transparent;
            @media #{$small-only}{
                display: inline-block;
                width: 100%;
                margin-bottom: -5px; 
            }
            .category-drpdown-display{
                @media #{$small-only}{
                    margin-bottom: 0;
                } 
            }
    }
    .nav-carousel-slides {
        padding: em(10) em(40);
        background-color: $filter-nav-carousel-bg;
        .slick-next,
        .slick-prev {
            width: auto;
            height: auto;
            background-color: $litviolet;
            padding: 5px 16px;
            @include border-radius(50%);
            &:before {
                color: $white;
                font-size: 35px;
                opacity: 1;
                padding: 0;
            }
        }
        .owl-nav{
            button{
                &:before{
                    color: $filter-nav-arrow-color;
                }
            }
        }
        li {
            width: auto;
            margin-right: 0;
            a {
                background-color:$filter-nav-carousel-link-bg;
                border: 4px solid $filter-nav-carousel-link-border;
                font-family: 'Swis',Arial;
                font-weight: $fontRegular;
                font-size: em(19);
                letter-spacing: 1px;
                min-width:100px;
		    	width:auto;
                @include border-radius(em(50,19));
                padding: em(10,19) em(25,19);
                &:hover,
                &:focus, &.active {
                    color: $filter-nav-carousel-link-bg;
                    background-color: $filter-nav-carousel-link-hover-bg;
                }
            }
        }
    }
}

.video-play-list-area {
    .playing-overlay {
        display: none;
    }
}

.videos-gallery-playlist {
    @extend .col-md-12;
    padding: 0;
    @media #{$small-up}{
        display: inline-block;
    }
    h2 {
        font-family: mapping($videosLanding,thumbnailHeading,fontFamily);
        font-weight: mapping($videosLanding,thumbnailHeading,fontWeight);
        background-color: mapping($videosLanding,thumbnailHeading,bgColor);
        padding: if(mapping($videosLanding,thumbnailHeading,padding), mapping($videosLanding,thumbnailHeading,padding),20px 54px 0);
        font-size: if(mapping($videosLanding,thumbnailHeading,fontSize), mapping($videosLanding,thumbnailHeading,fontSize), em(24));
        color:if(mapping($videosLanding,thumbnailHeading,color), mapping($videosLanding,thumbnailHeading,color), $violet);
        margin:if(mapping($videosLanding,thumbnailHeading,margin), mapping($videosLanding,thumbnailHeading,margin), 0);
        text-align: mapping($videosLanding,thumbnailHeading,align);
        text-transform:uppercase;
        @media #{$medium-down} {
            padding: if(mapping($videosLanding,thumbnailHeading,tabPadding), mapping($videosLanding,thumbnailHeading,tabPadding),0 em(20));
        }
        @media #{$small-down} {
            padding: if(mapping($videosLanding,thumbnailHeading,mobPadding), mapping($videosLanding,thumbnailHeading,mobPadding),0 em(10));
        }
    }
    
    .video-heading{
        	color:$violet;
        }

    .play-tiles-gallery {
        .gallery-tile {
            padding: if(mapping($videosLanding,galleryTile,tilePadding), mapping($videosLanding,galleryTile,tilePadding), 0 2.14286em 1.07143em);
			@media #{$medium-down} {
                padding: if(mapping($videosLanding,galleryTile,tabTilePadding), mapping($videosLanding,galleryTile,tabTilePadding), 0 2.14286em 1.07143em);
            }
			@media #{$small-down} {
                padding: if(mapping($videosLanding,galleryTile,mobTilePadding), mapping($videosLanding,galleryTile,mobTilePadding), 0 1.42857em);
            }
			h2{
				padding: 0 25px;
				@media #{$medium-down} {
					padding: 0 25px;
				}
				@media #{$small-down} {
					padding: 0;
				}
			}
			.view-all{margin-right:25px;}
            ul {
                padding: 0 0;
                
                &>li {
                    padding: em(25);
                    @media #{$small-down} {
                        padding: em(10) 0;
                    }
                    a {
                        .gallery-image{
                            position: relative;
                            @if(mapping($videosLanding, main, isRoundedPlayIcon), 'true'){
                                &:after{
                                    content: "\f04b";
                                    @if(mapping($videosLanding, playIcon, isRoundedCircle), 'true'){
                                        border-radius: 60px;
                                        padding: 0.5em 0.6em;
                                    }
                                    color: mapping($videosLanding, playIcon, color);
                                    background-color: mapping($videosLanding, playIcon, bgnd);
                                    position: absolute;
                                    left: 50%;
                                    transform: translate(-50%);
                                    -webkit-transform: translate(-50%);
                                    -moz-transform: translate(-50%);
                                    -ms-transform: translate(-50%);
                                    font-size: mapping($videosLanding, playIcon, mobFontSize);
                                    top: calc(50% - #{mapping($videosLanding, playIcon, mobFontSize)-7px});
                                    opacity: .9;
                                    @include font-awesome();
                                    @media #{$small-up}{
                                        font-size: mapping($videosLanding, playIcon, tabFontSize);
                                        top: calc(50% - #{mapping($videosLanding, playIcon, tabFontSize)-7px});
                                    }
                                    @media #{$medium-up}{
                                        font-size: mapping($videosLanding, playIcon, fontSize);
                                        top: calc(50% - #{mapping($videosLanding, playIcon, fontSize)-7px});
                                    }
                                }
                            }
                        }
                        span {
                            background-color: mapping($videosLanding,listTitle, bgColor);
                            color: mapping($videosLanding,listTitle, color);
                            text-align: if(mapping($videosLanding,listTitle, align), mapping($videosLanding,listTitle, align), center);
                            font-family: if(mapping($videosLanding,listTitle, fontFamily), mapping($videosLanding,listTitle, fontFamily), Arial);
                            font-size: if(mapping($videosLanding,listTitle, fontSize), mapping($videosLanding,listTitle, fontSize), 1em);
                            text-transform: uppercase;
                            letter-spacing: 1px;
                            padding: if(mapping($videosLanding,listTitle, padding), mapping($videosLanding,listTitle, padding), em(12));
                            margin: mapping($videosLanding,listTitle, margin);
                            font-weight: mapping($videosLanding,listTitle, fontWeight);
                        }
                        &:hover{
                            .playing-overlay {
                                display: block;
                            }        
                        }
                        .playing-overlay {
                            display: block;
                            position: absolute;
                            top: 0;
                            bottom: 0;
                            left: 0;
                            right: 0;
                            background-color: transparent;
                            span {
                                position: absolute;
                                z-index: 1;
                                &.play-icon {
                                    @if(mapping($videosLanding, main, isRoundedPlayIcon), 'true'){
                                        display:none;
                                    } @else{
                                        top: 30%;
                                    }
                                    left: 50%;
                                    @include transform(translateX(-50%));
                                    opacity: 0.9;
                                    font-family: 'Font Awesome';
                                    background: transparent;
                                    color: $white;
                                    padding: 0;
                                    font-size: 45px;
                                    line-height: 1;
                                }
                                &.play-text {
                                    display: none;
                                }
                            }
                        }
                    }
                    &.active {
                        a {
                            .gallery-image{
                                @if(mapping($videosLanding, main, isRoundedPlayIcon), 'true'){
                                    &:after{
                                        content:none;
                                    }
                                }
                            }
                            .playing-overlay {
                                display: block;
                                background-color: rgba(0, 0, 0, 0.1);
                                span {
                                    &.play-icon {
                                        display: none;
                                    }
                                    &.play-text {
                                        top: 7px;
                                        left: 7px;
                                        font-size: if(mapping($videosLanding, nowPlaying, fontSize),mapping($videosLanding, nowPlaying, fontSize), 1em);
                                        font-family: if( mapping($videosLanding, nowPlaying, fontFamily), mapping($videosLanding, nowPlaying, fontFamily),"Arial");
                                        white-space: nowrap;
                                        display: inline-block;
                                        letter-spacing: 1px;
                                        color: if(mapping($videosLanding, nowPlaying, color),mapping($videosLanding, nowPlaying, color), $violet);
                                        background-color: if(mapping($videosLanding, nowPlaying, backgroundColor), mapping($videosLanding, nowPlaying, backgroundColor), $white);
                                        border-color: mapping($videosLanding, nowPlaying, borderColor);
                                        padding: if(mapping($videosLanding, nowPlaying, padding), mapping($videosLanding, nowPlaying, padding), em(7));
                                        margin: mapping($videosLanding, nowPlaying, margin);
                                        @include border-radius(if(mapping($videosLanding, nowPlaying, borderRadius),mapping($videosLanding, nowPlaying, borderRadius),0));
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.cd-prev,
.cd-next {
    top: 40%;
    z-index: 1024;
    width: auto;
    height: auto;
    @media #{$small-down} {
        top: 35%;
    }
    &:before {
        font-family: 'Font Awesome';
        @if(mapping($videosLanding,arrows,isRoundBgnd),'false'){
            color: mapping($videosLanding,arrows,color);
            background: none;
        } @else{
            background-color: $litviolet;
            @include border-radius(50%);
            padding: 15px 23px;
        }
        opacity: if(mapping($videosLanding,arrows,opacity), mapping($videosLanding,arrows,opacity),0.9);
        font-size: if(mapping($videosLanding,arrows,fontSize), mapping($videosLanding,arrows,fontSize), 36px);
        @media #{$small-down} {
            background-color: transparent;
            color: if(mapping($videosLanding,arrows,mobColor), mapping($videosLanding,arrows,mobColor), $litviolet);
            padding: 0;
        }
    }
}

.cd-prev {
    @extend .slick-prev;
    &:before {
        content: "\f053";
    }
    left:if(mapping($videosLanding, arrows, leftPos), mapping($videosLanding, arrows, leftPos), 9px);
}

.cd-next {
    @extend .slick-next;
    &:before {
        content: "\f054";
    }
    right:if(mapping($videosLanding, arrows, rightPos), mapping($videosLanding, arrows, rightPos), 9px);
}

//checkbox

/**
* Checkbox Three
*/

.autoplay-label {
    float: left;
    padding-top: 6px;
    font-family: arial;
}

.autoplay-checkbox {
    width: 62px;
    height: 30px;
    background: $violet;
    margin: 0;
    @include border-radius(50px);
    position: relative;
    float: right;
    /**
    * Create the text for the On position
    */
    &:before,
    &:after {
        position: absolute;
        top: 8px;
        height: 2px;
        font-size: 11px;
    }
    &:before {
        content: 'On';
        left: 6px;
        color: $white;
    }
    /**
    * Create the label for the off position
    */
    &:after {
        content: 'Off';
        right: 6px;
        color: $litviolet;
    }
    label {
        width: 22px;
        height: 22px;
        @include border-radius(em(22));
        @include transition(all .5s ease);
        cursor: pointer;
        position: absolute;
        top: 4px;
        z-index: 1;
        left: 4px;
        background: $litviolet;
        margin: 0;
    }
    /**
    * Create the checkbox event for the label
    */
    input[type=checkbox]:checked + label {
        left: 36px;
        background: $white;
    }
    input[type=checkbox] {
        visibility: hidden;
    }
}
