						}
					}
				}
			}
		}
}
    
#video-gallery-player-component {
    @if(mapping($videosLanding,homePage,pageBgnd)){
        background: url(mapping($videosLanding,homePage,pageBgnd));
        overflow: hidden;
        .play-tiles-gallery{
            background: none;
        }
    }
    .video-player-in{
            @extend .col-md-12;
            padding: if(mapping($videosLanding,homePage,playerPadding), mapping($videosLanding,homePage,playerPadding), 0);
	}
	.view-all {
        @if(mapping($videosLanding, main,isViewAllHide), 'true'){
