$chardetails:(

main:(
    bgnd: url('https://play.pollypocket.com/sites/mattel_pollypocket/files/2018-04/product-bg_2.jpg'),
    pad: 50px 0
),

cdaBanner: ( 
    width:80%,
    margin: 0 auto !important,
    pos: relative,
    bgnd : $white,
),

bannerCarousel:(
   pad:10px 0,
   lstpos: relative,
   tabimgWidth: 300px !important,
   imgfloat:right
),

carouselslickprevNext: (
    posTop: 30%,
    tabposTop:50%,
    fntFamily: $FontAwesome,
    bgndColor: $litviolet,
    borderRadius: 50%,
    pad: 5px 10px
),

carouselslickPrev: (
    posLeft: -25px,
    prevContent: "\f104"
),

carouselslickNext: (
    posRight: -25px,
    nextContent: "\f105"
),

bannerCopy: (
    tabWidth:48%,
    pad: em(25),
    bgnd:$white,
    posTransform: none,
    posTop: auto,
    posLeft: auto
 ),

bannercopyH2: (   
    txtColor: $pink  
 ),

bannercopyP: (
  txtColor: $violet
),

bannercopytopLeft: (
    posLeft: 5%,
    posTop: 8%
),
bannercopytopRight: (
    posRight: 5%,
    posTop: 8%
),
bannercopybottomLeft: (
    posLeft: 5%,
    posBottom: 8%
),
bannercopybottomRight: (
    posRight: 5%,
    posBottom: 8%
),

bannercopytopCenter:( 
    posLeft: 5.686274509803922%,
    posTop: 50%,
    posTransform: translateY(-50%)
)

);