$countrydropdown:(

dropdownMenu:(
  margin: 0 auto,
  pad: 0,
  width: 100%,
  maxWidth: 254px,
  txtAlign: left,
  fntSize: 0.875em,
  mobmarginBottom: em(20),
  tabmarginBottom: em(30)
),

 dropdownList: ( 
    pos: relative,
    pad: 0,
    bgnd: $black,
    lstStyle: none,
    display: inline-block,
    width: 100%
 ), 

 dropdownSpan: (
    brdr: $blue 1px solid,
    txtColor: $blue,
    display: block,
    fntSize: em(22),
    pad: em(1) em(10),
    bgnd: $darkGrey,
    spantransition: all 0.2s ease-in,
    hoverbgnd:$darkGrey,
    hoverColor: $blue,
    spanContent: '\f078',
    fntFamily: "Font Awesome",
    pos: absolute,
    posTop: 1px,
    posRight: 1px,
    width: 32px,
    height: 29px,
    paddingTop: 3px,
    txtAlign: center,
    spanafterBgnd: $darkestGrey,
    spanaftertxtColor: $blue,
    boxSizing: border-box
 ),

 dropdownUl: (
    pos: absolute,
    width: 100%,
    pad: 0,
    z-index: 999,
    height: 100px
 ),

 dropdownLink: (  
    display: block,
    pad: em(3) em(10),
    textDecoration: none,
    txtColor: $blue,
    transition: all 0.2s ease-in, 
    hoverBgnd: $blue,
    hovertxtColor: $black,
    fntSize: em(22)
 ),

 dropdownAll: (   
    point: cursor
 ),

  menuLevel: (
    display: none,
    menuopenDisplay: block,
    height: 5em,
    overflow: hidden,
    overflowY: scroll,
    boxShadow: inset 0 0 6px rgba(0, 0, 0, 0.3),
    webkitboxShadow: inset 0 0 6px rgba(0, 0, 0, 0.3),
    borderRadius: 0,
    bgndColor: $blue,
    webkitscrollWidth: 7px,
    webkitscrollbgndColor: $white,
    webkitscrollHeight: 6px

  )

);