$layout:(
	body:(
		font: "Sunflower",
		fontWeight: $fontMedium
	),
	main: (
		titleMargin: 5rem 3.375rem 3.125rem 3.375rem,
		tabTitleMargin: 1.875rem,
		mobTitleMargin: 1.25rem,
		sectionMargin: 0rem 3.375rem 0 3.375rem,
		tabSectionMargin: 0rem 1.875rem,
		mobSectionMargin: 0rem 1.25rem
	),
	heading:(
		font: "DecoturaICG",
		fontWeight: $fontLight	
	),
	themeBtn:(
		fontSize: em(30),
		mobFontSize : em(25),
		padding: em(13, 30) em(20, 30) em(15, 30) em(35, 30),
		fontFamily:'DecoturaICG',
		fontWeight: $fontMedium,
		iscurveShape: true,
		bgndColor: #4f95cb,
		hoverBgndColor:#191919,  
		clipPath: polygon(10% 0, 100% 0, 100% 100%, 0 100%, 0 40%),
		color: #4f95cb,
		hoverColor: #191919 !important,
		borderSize: 2px,
		borderRadius: 0,
		letterSpacing: 0,
		isnextArrw: true,
		nextContent:"",
		contentFamily:"",
		contentWeight: "",
		contentMargin: ""
	),
	secThemeBtn:(
		bgndColor: #bc3227,
		isArrow: false,
		padding: em(13, 30) em(38, 30) em(15, 30) em(30, 30),
		isPlus: true,
		hoverBgndColor:#191919,  
		color: #bc3227,
		hoverColor: #191919 !important,
	),
	viewAll: (
		isOverwritePrimaryBtn: true
	),
	dots:(
		fontSize: 14px,
        opacity: .5,
		focusColor: $light-green,
        bgndColor: $lightGrey,
		activeBgndColor: #4f95cb,
		margin: 0 7px !important,
		bottomPositon: 35px,
	),
	arrow: (
		color: #4f95cb,
		isRoundedBgnd:0
  ),
  gototop: (
    bgndColor: $darkGrey,
    brdrColor: $blue 1px solid,
    fntColor: $blue,
    fntFamily: 'Sunflower',
    fntSize: em(14),
    arrowShape: 10px solid $blue
  )
);