.bg-image{
	background-position: 90% center;
	background-size: 15em;
	background-repeat: no-repeat;
	width: 100%;
	height: auto;
	vertical-align: bottom;
	&.mobile {
		display: none;
	}
    @media #{$small-down} {
        &.desktop {
			display: none;
		}
        &.mobile {
			display: block;
		}
	}
}
.mall-tour-page{
    @extend .col-md-12;
    @extend .col-sm-12;
    padding:0;
    padding-bottom:130px;
    display: block;
    .mall-banner{
        @extend .col-md-12;
        @extend .col-sm-12;
        display: block;
        padding:0;
        background-color:#6a17a5;
        @media #{$small-down} {
            min-height:87px;
        }
    }
    .mall-text-block{
        
        text-align: center;
        @extend .col-md-12;
        @extend .col-sm-12;
        padding:em(65);

        h2{
            color:$violet;
            margin:0;
            margin-bottom:30px;
            font-size:em(38);
            font-family:'Swiss';
            letter-spacing: 1px;
            font-weight:$fontLight;
            text-transform: uppercase;
        }

        p{
            color:$pink;
            margin:0;
            font-family:'Arial',sans-serif;
            font-size:em(19);
        }
         @media #{$small-down} {
             padding:33px 22px 22px;
         }

    }
    .mall-time-schedule{
        @extend .col-md-12;
         @extend .col-sm-12;
        display:block;
        padding:0 55px;
        @media #{$small-down} {
             padding:0;
         }
        .schedule-details{
            @extend .col-md-12;
             @extend .col-sm-12;
            display:inline-block;
            text-align: center;
            text-transform: uppercase;
            padding:20px 0;
            border-top:4px solid rgba(238, 202, 236, 0.8);
            color:$violet;
            font-size: em(20);
            vertical-align: middle;
            &:last-child{
                border-bottom:4px solid rgba(238, 202, 236, 0.8);
            }
            

            
        .time-details{
            @extend .col-md-4;
            @extend .col-sm-12;
            font-family:'Swiss';
            display:inline-block;
            vertical-align: middle;
            font-size: 1.3em;
            line-height: 2.5em;
            font-weight:$fontLight;
        }
        .location-details{
            @extend .col-md-4;
             @extend .col-sm-12;
            display:inline-block;
            vertical-align: middle;
            font-size: 1.2em;
            line-height: 2.9em;
            font-family:'Arial';
        }
        .link-details{
            @extend .col-md-4;
            @extend .col-sm-12;
            display:inline-block;
            vertical-align: middle;
            font-family:'Swiss';
            .btn-play-default{
                font-size:16px;
                min-width: 265px;
                border-width: 4px;
                padding: 15px;
                font-weight: 100;
                 &:link{
                     color:$white;
                 }
            }

        }
        }
    }
}
