.accordion-component {
  background-color: mapping($accordion,main,bgndColor);
  &.withbg {
    background: mapping($accordion,withbg,bgndImg);
    background-size: mapping($accordion,withbg,bgndSize);
    .accordion {
      .accordion-title {
          font-family: mapping($accordion,withbg,fntFamily);
          font-size: mapping($accordion,withbg,fntSize);
        }
      }
  }
  .title-block {
    padding: mapping($accordion,main,titleBlockPad);
  }
  .accordion {
    padding: mapping($accordion,main,pad);
    .accordion-title {
        position: relative;
        margin: mapping($accordion,title,margin);
        padding: mapping($accordion,title,pad);
        font-weight: mapping($accordion,title,fntWeight);
        border-bottom: mapping($accordion,title,borderBottom);
        color: mapping($accordion,title,txtColor);
        font-size: mapping($accordion,title,fntSize);
        border-top: mapping($accordion,title,borderTop);
        font-family: mapping($accordion,title,fntFamily);
        .view {
            font-size:   mapping($accordion,view,fntSize);
            line-height: mapping($accordion,view,lineHeight);
            font-weight: mapping($accordion,view,fntWeight);
            text-align:  mapping($accordion,view,txtAlign);
        }
    }
    >div {
      &:last-child {
        .accordion-title {
          border-bottom: mapping($accordion,title,borderTop);
        }
      }
    }
    .accordion-content {
        display: mapping($accordion,accContent,display);
        padding: mapping($accordion,accContent,pad);
        &.default {
            display: mapping($accordion,acccontentDefault,display);
        }
        p {
          font-weight: mapping($accordion,accContentP,fntWeight);
          color: mapping($accordion,accContentP,txtColor);
          font-size: mapping($accordion,accContentP,fntSize);
          font-family: mapping($accordion,accContentP,fntFamily);
          padding: mapping($accordion,accContentP,pad);
          margin: mapping($accordion,accContentP,margin);
        }
        ol {
          li {
            list-style-type: inherit;
            font-weight: mapping($accordion,accContentOlLi,fntWeight);
            color: mapping($accordion,accContentOlLi,txtColor);
            font-size: mapping($accordion,accContentOlLi,fntSize);
            font-family: mapping($accordion,accContentOlLi,fntFamily);
            padding: mapping($accordion,accContentOlLi,pad);
            margin: mapping($accordion,accContentOlLi,margin);
          }
        }
    }
    .arrow {
        margin-left: mapping($accordion,accArrow,marginLeft);
        display:  mapping($accordion,accArrow,display);
        position: mapping($accordion,accArrow,pos);
        right:   mapping($accordion,accArrow,posRight);
        border: mapping($accordion,accArrow,brdr);
        border-radius: mapping($accordion,accArrow,brdrRadius);
        width: mapping($accordion,accArrow,iconWidth);
        height: mapping($accordion,accArrow,iconHeight);
        cursor: mapping($accordion,accArrow,cursor);
        top: 50%;
        transform: translateY(-50%);
        &:before,
        &:after {
          content: '';
          display: block;
          border-radius: 1em;
          @include transform(translate(-50%,-50%));
          top: 50%;
          left: 50%;
          position: absolute;
          @include transition(all,350ms,ease-out);
          background: mapping($accordion,accArrow,iconColor);
        }
        &.arrowDown {
          &:before {
            width: 4px;
            height: 75%;
          }
          &:after {
            width: 75%;
            height: 4px;
          }
        }
        &.arrowUp {
          &:before {
            width: 75%;
            height: 4px;
          }
        }
    }
  }
}