$errorpagecomponent:(
  main: (
    bgImage: $grey url($img-txt-bg-path) 0 0 repeat,
    topPad: em(70),
    tabPortraitTopPad: 0,
    mobTopPad: em(50)
  ),
  imgBlock: (
    pad: 0 em(50) 0 0,
    tabPortraitPad: 0 em(100) 0 0,
    mobPad: 0 em(40) 0 0
  ),
  txtBlock: (
    pad: em(20) em(40),
    tabPortraitPad: em(40) em(30),
    mobPad: 0 em(20) em(40)
  ),
  txtBlockH2: (
    fntColor: $darkestGrey,
    fntSize: em(65),
    margin: 0
  ),
  txtBlockP: (
    fntColor: $darkestGrey,
    fntFamily: inherit
  ),
  errorCTA: (
    marginTop: em(20),
    sameasPrimaryBtn: true,
    isArrowWithBtn: false,
    padding: em(13, 30) em(35, 30) em(15, 30) em(35, 30),
    display: 0
  ),
  errorCTAAnchor: (
    display: 0,
    fntColor: 0
  ),
  fullWidth: (
    pad: 0,
    topPad: 0
  )
);