.char-detials-comp{
    background: mapping($chardetailsslider,main,bgnd);
    padding: mapping($chardetailsslider,main,pad);
    position: mapping($chardetailsslider,main,pos);
    .container{
        background-color: mapping($chardetailsslider,container,bgndColor);
         padding: mapping($chardetailsslider,container,pad);
         @media #{$small-down}{
             width: mapping($chardetailsslider,container,tabWidth);
        }
         @media #{$medium-only}{
             width:mapping($chardetailsslider,container,mobWidth);   
        }
    }
    .char-detials-content{
        @extend .row;
        margin-left: mapping($chardetailsslider,chardetailContent,marginLeft);
        margin-right: mapping($chardetailsslider,chardetailContent,margiRight);
    }
    .char-details-image{
        @extend .col-xs-12;
        @extend .col-sm-12;
        @extend .col-md-6;
        text-align: mapping($chardetailsslider,chardetailImage,txtAlign);
        padding: mapping($chardetailsslider,chardetailImage,pad);
        &.img-right{
            float: mapping($chardetailsslider,chardetailImage,float);
        }
        @media #{$medium-only}{
            img{
                max-width: mapping($chardetailsslider,chardetailImage,tabmaxWidth);
                padding-top: mapping($chardetailsslider,chardetailImage,tabpaddingTop);
            }
        }
    }
    .char-details-description{
        @extend .col-xs-12;
        @extend .col-sm-12;
        @extend .col-md-6;
        padding-bottom: mapping($chardetailsslider,chardetailDesc,paddingBottom);
        pad: mapping($chardetailsslider,chardetailDesc,pad);
        h1, h2{
            color: mapping($chardetailsslider,chardetaildescH1,txtColor);
            font-family: mapping($chardetailsslider,chardetaildescH1,fntFamily) , Sans-serif;
            font-weight: mapping($chardetailsslider,chardetaildescH1,fntWeight);
            small{
                color: mapping($chardetailsslider,chardetaildescH1,smalltxtColor);                
            }
        }
        p{
            color: mapping($chardetailsslider,chardetaildescP,txtColor);
            font-family: mapping($chardetailsslider,chardetaildescP,fntFamily) , Sans-serif;
            font-weight: mapping($chardetailsslider,chardetaildescP,fntWeight);
            span {
                font-family: mapping($chardetailsslider,chardetaildescP,fntFamily)  , Sans-serif;
            }

        }

    }
     .cd-prev { 
            @extend .slick-prev;
            left: mapping($chardetailsslider,cdPrev,posLeft);
            @media (max-width: 1024px){
           		left: mapping($chardetailsslider,cdPrev,desposLeft);
            }
            @media #{$medium-only}{
                left: mapping($chardetailsslider,cdPrev,tabposLeft);
            }
            @media  #{$small-down}{
           		left: mapping($chardetailsslider,cdPrev,mobposLeft);
            }
            &:before {
                content: mapping($chardetailsslider,cdPrev,prevContent);
            }
        }
        .cd-next { 
            @extend .slick-next;
            right: mapping($chardetailsslider,cdNext,posRight);
            @media (max-width: 1024px){
           		right: mapping($chardetailsslider,cdNext,desposRight);	
            }
            @media #{$medium-only}{
                right: mapping($chardetailsslider,cdNext,tabposRight);
            }
             @media  #{$small-down}{
           		right: mapping($chardetailsslider,cdNext,mobposRight);
            }
            &:before {
                content: mapping($chardetailsslider,cdNext,nextContent);
            }
        }

        .cd-prev,
        .cd-next{
            top: mapping($chardetailsslider,cdprevNext,posTop);
            width: mapping($chardetailsslider,cdprevNext,width);
            height: mapping($chardetailsslider,cdprevNext,height);
            border-radius: mapping($chardetailsslider,cdprevNext,borderRadius);
            padding: mapping($chardetailsslider,cdprevNext,pad);
            background: mapping($chardetailsslider,cdprevNext,bgnd);
            &:hover, &:focus, &:active{
                background: mapping($chardetailsslider,cdprevNext,hoverBgnd);
            }    
            @media #{$small-down}{
             /*display: none;*/
            }
            @media #{$medium-only}{
             top: mapping($chardetailsslider,cdprevNext,tabposTop);
            }
            &:before {
                font-family: mapping($chardetailsslider,cdprevnextBefore,fntFamily);
                background-color: mapping($chardetailsslider,cdprevnextBefore,bgndColor);
                border-radius: mapping($chardetailsslider,cdprevnextBefore,borderRadius);
                padding: mapping($chardetailsslider,cdprevnextBefore,pad);
                font-size: mapping($chardetailsslider,cdprevnextBefore,fntSize);
                opacity: mapping($chardetailsslider,cdprevnextBefore,opacity);
                background: mapping($chardetailsslider,cdprevnextBefore,bgnd);
            }
        }
}