.extend-middle {
  display: inline-block;
  vertical-align: middle;
  float: none;
}
.product-gallery-module {
  padding: em(50);
  overflow: hidden;
  color: $white;
  @media #{$medium-down} {
    display: table;
    table-layout: fixed;
    max-width: 100%;
    width: 100%;
  }
  @media #{$small-only} {
    padding: em(20);
  }
  .product-gallery {
    &.col-md-6 {
      @media #{$medium-up} {
        padding-right: em(30);
      }
    }
    .larger-product-images {
      li {
        @extend .extend-middle;
        img {
          margin: auto;
        }
      }
    }
    .small-product-images {
      li {
        cursor: pointer;
        width: 100%;
        height: 100%;
        position: relative;
        @extend .extend-middle;
        &:after {
          content: "";
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          z-index: 1;
          opacity: 0;
          border-radius: 100em;
          // -webkit-transition: 150ms;
          // -o-transition: 150ms;
          // transition: 150ms;
        }
        @media #{$medium-up} {
          &:hover:after {
            opacity: 0.5;
          }
        }
        &.slick-current:after {
          opacity: 0.5;
        }
      }
      @extend .slick-arrow-extend;
    }
  }
  div.product-heading {
    @media #{$medium-down} {
      display: table-header-group;
      background: none;
    }
  }
  .product-description {
    .theme-btn {
      background-color: $banner-bg;
      border: em(3) solid $banner-border;
      color: $banner-txt;
      &:hover {
        background: $banner-border !important;
        border: em(3) solid $banner-bg;
      }
      // @extend .banner-btn-theme;
    }
  }
  .close-gallery-modal {
    display: none;
    padding: 0.3em 0.3em;
    float: right;
    margin-bottom: 10px;
    clear: left;
    font-size: em(30);
    cursor: pointer;
    position: relative;
    z-index: 2;
    &:after {
      content: "\f00d";
      @include font-awesome();
      color: $black;
    }
  }
}

.product-gallery-module {
  .product-gallery {
    .small-product-images {
      li {
        max-width: mapping($pdp, smallThumbnail, width);
        max-height: mapping($pdp, smallThumbnail, height);
        margin: mapping($pdp, smallThumbnail, margin);
        &:after {
          background: mapping($pdp, smallThumbnail, hoverBgnd);
        }
      }
    }
  }
  .product-heading {
    background-color: mapping($pdp, heading, bgColor);
    text-align: mapping($pdp, heading, align);
    padding: mapping($pdp, heading, padding);
    h2 {
      color: mapping($pdp, heading, color);
      font-size: mapping($pdp, heading, fontSize);
      font-family: mapping($pdp, heading, fontFamily), Arial, Helvetica,
        sans-serif;
      font-weight: mapping($pdp, heading, fontWeight);
      margin: mapping($pdp, heading, margin);
    }
  }
  .product-description {
    background-color: mapping($pdp, description, bgColor);
    color: mapping($pdp, description, color);
    font-size: mapping($pdp, description, fontSize);
    font-family: mapping($pdp, description, fontFamily);
    font-weight: mapping($pdp, description, fontWeight);
    text-align: mapping($pdp, description, align);
    padding: mapping($pdp, description, padding);
  }
  .theme-btn {
    font-size: mapping($pdp, wtb, fontSize);
    padding: mapping($pdp, wtb, padding);
  }
}
@if (mapping($pdp, largeThumbnail, isModalInMobile) == true) {
  .has-touch {
    .product-gallery-module {
      .product-gallery.open-modal {
        position: fixed;
        top: 0;
        bottom: 0;
        z-index: 1111;
        height: 100%;
        background: $white;
        width: 100%;
        left: 0;
        .larger-product-images {
          @media screen and (orientation: landscape) {
            li{
              vertical-align: top;
              img{
                max-height: 72vh;
              }
              .product-player-container .innerWrapper{
                padding-top: 0 !important;
                height: 72vh;
              }
            } 
          }
          .slick-list {
            overflow: inherit;
          }
        }
        @media screen and (orientation: landscape) {
          .close-gallery-modal{
            position: absolute;
            right: 15px;
          }
        }
        .small-product-images {
          position: absolute;
          bottom: 0;
          max-width: 85%;
          left: 50%;
          @include transform(translateX(-50%));
          .slick-slider{
            margin-bottom: 2px;
          }
        }
        .close-gallery-modal {
          display: block;
        }
      }
    }
  }
}
