.play-character-landing-block {
    @extend .container-fluid;
    >div {
        @extend .row;
        >div {
            @extend .col-xs-12;
            @extend .col-sm-6;
            @extend .col-md-4;
            padding: mapping($characterlanding,childDiv,pad);
            text-align: mapping($characterlanding,childDiv,txtAlign);
            position: mapping($characterlanding,childDiv,pos);
            section {
                text-align: mapping($characterlanding,charSection,txtAlign);
                position: mapping($characterlanding,charSection,pos);
                img {
                    @extend .img-responsive;
                }
                span {
                    background-color: mapping($characterlanding,charSpan,bgndColor);
                    @include border-radius(mapping($characterlanding,charSpan,borderRadius));
                    text-align: mapping($characterlanding,charSpan,txtAlign);
                    padding: mapping($characterlanding,charSpan,pad);
                    font-size: mapping($characterlanding,charSpan,fntSize);
                    color: mapping($characterlanding,charSpan,txtcolor);
                    position: mapping($characterlanding,charSpan,pos);
                    bottom: mapping($characterlanding,charSpan,posBottom);
                    left: mapping($characterlanding,charSpan,posLeft);
                    transform: mapping($characterlanding,charSpan,posTransform);
                }
            }
        }
    }
}
