//products-gallery
.play-products {
	background:url($product-gallery-bg-path) 0 0 repeat;
    
	.gallery-title {
		background-color:$white;
        h2{
            font-weight: $fontLight;
        }
	}
    .category-title{
        h2{
            font-size:em(21);
            font-weight: $fontLight;
            color:$white;
            padding: 25px 55px 10px;
        }
    }
    .gallery-tile {
        padding: if(mapping($layout,main,sectionMargin), mapping($layout,main,sectionMargin), em(0) em(30) em(15));
        @media #{$medium-down} {
            padding: mapping($layout,main,tabSectionMargin);
        }
        @media #{$small-down} {
            padding: mapping($layout,main,mobSectionMargin);
        }
        ul.product-list {
            &>li {
                margin-bottom:em(10);
                display: block;
                a {
					.gallery-image {
                        @include border-radius(50%);
                        overflow:hidden;
                        background-color:$white;
                        outline:none;
						&:hover {
                            img {
                                opacity:1;
                            }
                        }
                    }
                    
                    span {
						margin-top:em(15);
						@include border-radius(5px);
                        display:inline-block;
                        text-align:center;
                        font-size:em(21);
                        font-weight: $fontLight;
                        color: $violet;
                        background-color: $white; 
						/* Internet Explorer 10 */
                        display:-ms-flexbox;
                        -ms-flex-pack:center;
                        -ms-flex-align:center;

                        /* Firefox */
                        display:-moz-box;
                        -moz-box-pack:center;
                        -moz-box-align:center;

                        /* Safari, Opera, and Chrome */
                        display:-webkit-box;
                        -webkit-box-pack:center;
                        -webkit-box-align:center;

                        /* W3C */
                        display:box;
                        box-pack:center;
                        box-align:center; 
                    }
                }
            }
        }
        
    }
}


