.products-category-filter{
   .nav-carousel-slides {
       @media #{$small-up} {
        display:block;
        }
        /*@media #{$small-down} {
            display:none;
        }*/
        //background-color:$light-white;
        li{
            a{
                //min-width:150px;
                width:auto;
                font-family: 'Swiss';
                letter-spacing: 1px;
		padding: em(10) em(20);
                background-color: $pink;
                border: 3px solid $icegreen;
                color: $white;
                @include border-radius(em(20));
                &:hover,
                &:focus, &.active {
                    border-color: $icegreen;
                    background-color: $white;
                    color: $pink;
                }
            }
        }
   }
}
.products-page-title{
    @extend .video-page-title;
    background-image: url('../img/backpack.jpg');
    @media #{$small-down} {
        background-image: none;
    }
}

.products-gallery-list {
    @extend .col-md-12;
    padding: 0;
	@media #{$small-down} {
        margin-top:40px;
    }
    h2 {
        padding: 0 54px;
        font-size: em(21);
        @media #{$medium-down} {
            padding: 0 em(20);
        }
        @media #{$small-down} {
            padding: 0 em(10);
        }
        color:$white;
        margin:0;
        text-transform:uppercase;
    }
}
