$imagetextcomponent:(

imagetextTitle:(
    pad: em(10) 0,
    display: block,
    titlePadding: em(25)
),

 imagetextContainer: ( 
    margin: 0,
    pad: em(55) em(40),
    tabPad: null,
    mobPad: em(30) em(15),
    width: 100%,
    bgndDetail: $grey url($img-txt-bg-path) 0 0 repeat
 ), 
 imageBlockImg: (
  width: 80%
 ),
 imagetextblock: (
  pad: 0 em(40),
  tabPad: em(15) em(15) 0,
  mobPad: em(15) em(15) 0
 ),

 imagetextblockH2: (
    txtColor: $lightGrey,
    smalltxtColor: $white,
    margin:0,
    fntSize: em(60)
 ),

 imagetextblockP: (
   txtColor: $white,
   fntFamily: 'Sunflower',
   fntSize: em(20)
 ),

 imagetextblockLink: (  
   txtColor: $violet,
   txtDecoration: underline  
 ),

 imagetextCta: (   
     txtAlignLeft: left,
     txtAlignRight : right,
     txtAlignCenter: center,
     tabtxtAlign: center !important
  ),

  textcomp: (
    margin:0 !important
  )

);